<template lang="pug">
  .ring(:class="classObject")
    b-container(fluid)
      b-row
        b-col.ring__title-block(cols="12")
          h1 {{ title }}
      b-row.ring__row(:no-gutters="true")
        b-col.ring__text-block.ring__text-block--1(cols="12" md="5")
          .ring__text-wrapper
            p(v-for="eachParagraph in paragraph1" v-if="eachParagraph !== ''") {{ eachParagraph }}
            br(v-else)
        b-col(cols="12" md="7")
          img(:src="img1Src")
      b-row.ring__row(:no-gutters="true")
        b-col(cols="12" md="7" order="2" order-md="1")
          img(:src="img2Src")
        b-col.ring__text-block.ring__text-block--1(cols="12" md="5" order="1" order-md="2")
          .ring__text-wrapper
            p(v-for="eachParagraph in paragraph2" v-if="eachParagraph !== ''") {{ eachParagraph }}
            br(v-else)
      b-row
        b-col.ring__back-button(cols="2" offset="1")
          decorated-button(
            @click="$router.go(-1)"
            :color="'golden'"
            :buttonDescription="'BACK'")
</template>

<script>
import _ from 'lodash';
import ringInfo from '@/utils/engageRingInfo';
import DecoratedButton from '@/components/DecoratedButton.vue';

export default {
  data() {
    return {
      lang: 'zhTW',
      // lang: 'en',
      ringInfo,
    };
  },
  components: {
    DecoratedButton,
  },
  computed: {
    id() {
      return this.$route.params.id;
    },
    classObject() {
      return [this.id ? `ring--${this.id}` : ''];
    },
    specificRingInfo() {
      return ringInfo[this.lang][this.id]
        ? ringInfo[this.lang][this.id]
        : ringInfo[this.lang].distance;
    },
    title() {
      return _.has(this.specificRingInfo, 'title')
        ? this.specificRingInfo.title
        : '';
    },
    paragraph1() {
      return _.has(this.specificRingInfo, 'paragraph1')
        ? this.specificRingInfo.paragraph1
        : [];
    },
    paragraph2() {
      return _.has(this.specificRingInfo, 'paragraph2')
        ? this.specificRingInfo.paragraph2
        : [];
    },
    img1Src() {
      return this.id && require(`../assets/engageStoryRing/${this.id}/1@2x.jpg`); // eslint-disable-line 
    },
    img2Src() {
      return this.id && require(`../assets/engageStoryRing/${this.id}/2@2x.jpg`); // eslint-disable-line 
    },
  },
};
</script>

